import React from 'react';

export class Geometry {
    getRectangle(config) {
        return this.getTemplate(this.getRectangleDot, config);
    }

	getRectangleDot(i, params) {
		let style = {};

		const verticalDotsCount = 6;
		const horizontalDotsCount = 4;

		const side = {
			top: i < verticalDotsCount,
			right: i >= verticalDotsCount && i < verticalDotsCount + horizontalDotsCount,
			bottom: i >= verticalDotsCount + horizontalDotsCount && i < verticalDotsCount * 2 + horizontalDotsCount,
			left: i >= verticalDotsCount * 2 + horizontalDotsCount && i <= verticalDotsCount * 2 + horizontalDotsCount * 2
		};

		// Top side of rectangle
		if (side.top) {
			style = this.getRectangleTopSideDot(params, i, verticalDotsCount, horizontalDotsCount);
		} 
		
		// Right side of rectangle
		if (side.right) {
			style = this.getRectangleRightSideDot(params, i, verticalDotsCount, horizontalDotsCount);
		}

		// Bottom side of rectangle
		else if (side.bottom) {
			style = this.getRectangleBottomSideDot(params, i, verticalDotsCount, horizontalDotsCount);
		}

		// Left side of rectangle
		else if (side.left) {
			style = this.getRectangleLeftSideDot(params, i, verticalDotsCount, horizontalDotsCount);
		}

		return (
			<div key={i} style={style} className="dot"></div>
		);
	}
	
	getRectangleRightSideDot(params, i, verticalDotsCount, horizontalDotsCount) {
		const { dotsSize, width, height } = params;

		const x = this.getFirstDotCoords(width, dotsSize, verticalDotsCount);
		const y = this.getDotOffsetByDistance(height, dotsSize, horizontalDotsCount + 2, 10 - i);

		return this.getStyle({ x, y, bgColor: 'darkblue' });
	}

	getRectangleBottomSideDot(params, i, verticalDotsCount, horizontalDotsCount) {
		const { dotsSize, width, height } = params;
		const startedOffset = horizontalDotsCount + verticalDotsCount * 2 - 1;

		const x = this.getDotOffsetByDistance(width, dotsSize, verticalDotsCount, startedOffset - i);
		const y = this.getLastDotCoords(height, dotsSize, horizontalDotsCount + 2);

		return this.getStyle({ x, y, bgColor: 'red' });
	}

	getRectangleLeftSideDot(params, i, verticalDotsCount, horizontalDotsCount) {
		const { dotsSize, width, height } = params;

		const x = this.getLastDotCoords(width, dotsSize, verticalDotsCount);
		const y = this.getDotOffsetByDistance(height, dotsSize, horizontalDotsCount + 2, 20 - i);

		return this.getStyle({ x, y, bgColor: 'darkgreen' });
	}

	getRectangleTopSideDot(params, i, verticalDotsCount, horizontalDotsCount) {
		const { dotsSize, width, height } = params;

		const x = this.getDotOffsetByDistance(width, dotsSize, verticalDotsCount, i);
		const y = this.getFirstDotCoords(height, dotsSize, horizontalDotsCount + 2);

		return this.getStyle({ x, y, bgColor: 'lime' });
	}

	getDirectLine(config) {
		return this.getTemplate(this.getDirectLineDot, config);
    }

	getDirectLineDot(i, params) {
        let style = {};
        
        const { xOffset, yOffset, dotsSize } = params;

		let x = (dotsSize + xOffset) * i + xOffset,
            y = yOffset;

        style = this.getStyle({x, y});

		return (
			<div key={i} style={style} className="dot"></div>
		);
    }

    getTemplate(getDotFunction, config) {
        const { dotsCount, dotsSize, coords } = config;
        const dots = [];

        let xOffset = (coords.width - (dotsSize * dotsCount)) / (dotsCount + 1);
        let yOffset = coords.height / 2 - dotsSize / 2;

        if (xOffset < 0) {
            xOffset = 0;
		}

        const params = {
			dotsCount, dotsSize, xOffset, yOffset,
			width: coords.width, height: coords.height,
			rectangle: {
				top: {
					// xOffset: (coords.width - (dotsSize * 6)) / 7,
					yOffset: 50
				},
				bottom: {
					yOffset: coords.height - 50 - dotsSize
				},
				left: {
					xOffset: (coords.width - (dotsSize * 6)) / 7,
					yOffset: (coords.height - (dotsSize * 4)) / 5
				},
				right: {
					xOffset: (coords.width - (dotsSize * 6)) / 7,
					yOffset: (coords.height - (dotsSize * 4)) / 5
				}
			}
        };

		for (let i = 0; i < dotsCount; i++) {            
            dots.push(getDotFunction.call(this, i, params));
        }

		return dots;
	}
	
	/**
	 * Возвращает расстояние между точками
	 */
	getDistanceBetweenDots(templateSize, dotsSize, dotsCount) {
		return (templateSize - dotsSize * dotsCount) / (dotsCount + 1);
	}

	/**
	 * Возвращает смещение точки в зависимости от индекса
	 */
	getDotOffsetByIndex(distance, dotsSize, i) {
		return distance * (i + 1) + dotsSize * i;
	}

	/**
	 * Выполняет сначала getDistanceBetweenDots, затем getDotOffsetByIndex
	 */
	getDotOffsetByDistance(templateSize, dotsSize, dotsCount, counter) {
		const distance = this.getDistanceBetweenDots(templateSize, dotsSize, dotsCount);

		return this.getDotOffsetByIndex(distance, dotsSize, dotsCount - 1 - counter);
	}

	/**
	 * Возвращает отступ для первой точки
	 */
	getFirstDotCoords(templateSize, dotsSize, dotsCount) {
		const distance = this.getDistanceBetweenDots(templateSize, dotsSize, dotsCount);

		return this.getDotOffsetByIndex(distance, dotsSize, 0);
	}

	/**
	 * Возвращает отступ для последней точки
	 */
	getLastDotCoords(templateSize, dotsSize, dotsCount) {
		const distance = this.getDistanceBetweenDots(templateSize, dotsSize, dotsCount);

		return this.getDotOffsetByIndex(distance, dotsSize, dotsCount - 1);
	}

	getStyle({
		x = 0, 
		y = 0,
		bgColor = ''
	}) {
		const style = {};

		if (bgColor) {
			style.backgroundColor = bgColor;
		}

		style.transform = `translate3d(${x}px, ${y}px, 0)`;

		return style;
	}
};
