import React, { Component } from 'react';
import Button from 'react-bootstrap/Button';

import throttle from './Throttle';
import { Geometry as GeometryClass } from './Geometry';

import './index.scss';

const Geometry = new GeometryClass();

class App extends Component {
	constructor(props) {
		super(props);

		this.onButtonAnimateClick = this.onButtonAnimateClick.bind(this);

		this.figuresName = {
			rectangle: 'Прямоугольник',
			directLine: 'Линия'
		};

		this.buttonTexts = [
			this.figuresName.rectangle,
			this.figuresName.directLine
		];

		this.state = {
			buttonText: this.buttonTexts[0]
		};

		this.drawInitialDots = this.drawInitialDots.bind(this);

		this.initEvents();
	}

	initEvents() {
		this.makeCorrectWindowSize();

		window.addEventListener('resize', throttle.call(this, this.drawInitialDots, 250));
	}

	isMobileOrTablet() {
		return /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);
	}

	getInitialConfig() {
		const config = {
			dotsCount: 20,
			dotsSize: 20
		};

		const coords = document.querySelector('.template').getBoundingClientRect();
		config.coords = coords;

		const initianDraw = Geometry.getDirectLine(config);
		config.initianDraw = initianDraw;

		return config;
	}

	componentDidMount() {
		this.drawInitialDots();
	}

	makeCorrectWindowSize() {
		let vh = window.innerHeight * 0.01;
		
		document.documentElement.style.setProperty('--vh', `${vh}px`);

		window.addEventListener('resize', () => {
			let vh = window.innerHeight * 0.01;
			document.documentElement.style.setProperty('--vh', `${vh}px`);
		});
	}

	drawInitialDots() {
		const config = this.getInitialConfig();
		
		this.setState({
			...this.state,
			config,
			dots: config.initianDraw
		});
	}

	onButtonAnimateClick(e) {
		const { nextButtonText, dots } = this.getNextButtonTextAndDots(e.target.innerText);

		this.setState({
			...this.state,
			buttonText: nextButtonText,
			dots
		});
	}

	getNextButtonTextAndDots(buttonText) {
		const config = this.state.config;

		let nextButtonText,
			dots;

		if (buttonText === this.figuresName.rectangle) {
			nextButtonText = this.figuresName.directLine;
			dots = Geometry.getRectangle(config);
		} else if (buttonText === this.figuresName.directLine) {
			nextButtonText = this.figuresName.rectangle;
			dots = Geometry.getDirectLine(config);
		}

		return {
			nextButtonText,
			dots
		};
	}

	render() {
		const byDeviceText = this.isMobileOrTablet() ?
			' повернуть устройство в горизонтальное положение.' :
			' увеличить размер окна браузера.';

		return (
			<div className='App'>
				<div className='component'>
					<div className='template'>{this.state.dots}</div>
					<div className='actions'>
						{this.state.buttonText && <Button variant="primary" onClick={this.onButtonAnimateClick}>{this.state.buttonText}</Button>}
					</div>
				</div>
				<div className='device-info'>Для корректной работы необходимо {byDeviceText}</div>
			</div>
		);
	}
}

export default App;
